import React, {useEffect, useState} from "react";
import {useNavigate} from "react-router-dom";

interface GobackProps {
    where: string;
}

const Goback: React.FC<GobackProps> = ({where}) => {
    const navigate = useNavigate();
    const [isTooltipVisible, setIsTooltipVisible] = useState(false);

    const handleMouseEnter = () => {
        setIsTooltipVisible(true);
    };

    const handleMouseLeave = () => {
        setIsTooltipVisible(false);
    };

    let ment = "";

    if (where === "")
        ment = " AI , GPT             .";
    else if (where === "") ment = " \n  ,    ,       .\n           .";
    else ment = " \n   \n    .\n              .";

    const mentLines = ment.split('\n');

    return (
        <div
            style={{
                padding: "0.7rem",
                paddingBottom: "0rem",
                position: "absolute",
                zIndex: 30,
            }}
        >
            <img
                src={"/img/backIcon.png"}
                title={""}
                alt=""
                style={{width: "0.8rem", marginRight: "1rem", cursor: "pointer"}}
                onClick={() => navigate(-1)}
            />
            <span
                style={{
                    fontSize: "2.2rem",
                    fontWeight: "bold",
                    marginRight: "0.5rem",
                }}
            >
        {where}
      </span>
            <img
                src={"/img/Help.png"}
                alt=""
                style={{width: "1.4rem", cursor: "pointer"}}
                onMouseEnter={handleMouseEnter}
                onMouseLeave={handleMouseLeave}
            />

            {isTooltipVisible && (
                <div
                    style={{
                        position: "absolute",
                        backgroundColor: "rgba(217, 217, 217, 1 )",
                        left: "70%",
                        width: "20vw",
                        height: "18vh",
                        padding: "15px",
                        fontSize: "1rem",
                        borderRadius: "8px",
                    }}
                >
                    {mentLines.map((line, index) => (
                        <span key={index}>
                            {line}
                            <br />
                        </span>
                    ))}
                </div>
            )}
        </div>
    );
};

export default Goback;