import React, { useEffect, useState } from 'react';
import { GoogleOAuthProvider, GoogleLogin } from '@react-oauth/google';
import '../css/loginCss.scss';
import { useNavigate } from "react-router-dom";

const LoginPage: React.FC = () => {
    const [id, setId] = useState('');  //   
    const [password, setPassword] = useState('');
    const [rememberMe, setRememberMe] = useState(false);

    const navigate = useNavigate();

    // JWT  ,     
    useEffect(() => {
        const jwtToken = localStorage.getItem('jwtToken');  //   JWT 

        if (jwtToken) {
            //  JWT   
            fetch('http://localhost:8080/api/validate-token', {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${jwtToken}`,  // Bearer  
                    'Content-Type': 'application/json',
                }
            })
                .then(response => {
                    if (response.ok) {
                        // JWT    
                        navigate('/profile');
                    } else {
                        console.error('Token validation failed: ', response.status);
                        throw new Error('Unauthorized');
                    }
                })
                .catch(error => {
                    console.error('Error validating token:', error);
                });
        } else {
            console.log('JWT Token is missing');
        }
    }, [navigate]);

    //   
    const handleLogin = (e: React.FormEvent) => {
        e.preventDefault();
        const loginData = { id, password };  //   
        //    
        fetch('http://localhost:8080/api/manual-login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(loginData),
        })
            .then(async response => {
                if (!response.ok) {
                    const errorText = await response.text();
                    throw new Error(errorText);
                }
                return response.json();
            })
            .then(data => {
                const jwtToken = data.token;  //   JWT 
                const email = data.email;

                // JWT      
                localStorage.setItem('jwtToken', jwtToken);
                localStorage.setItem('email', email);

                //   
                navigate("/profile");
            })
            .catch(error => console.error('Error during manual login:', error));
    };

    //     
    const handleGoogleLoginSuccess = (credentialResponse: any) => {
        const idToken = credentialResponse.credential;  //   ID 

        //  POST   JWT 
        fetch('http://localhost:8080/api/google-login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            credentials: 'include',  //     
            body: JSON.stringify({ token: idToken }),  // ID  
        })
            .then(async response => {
                if (!response.ok) {
                    const errorText = await response.text();
                    throw new Error(errorText);
                }
                return response.json();
            })
            .then(data => {
                const jwtToken = data.token;  //   JWT 
                const email = data.email;

                // JWT      
                localStorage.setItem('jwtToken', jwtToken);
                localStorage.setItem('email', email);

                //   
                navigate("/profile");
            })
            .catch(error => console.error('Error during Google login:', error));
    };

    //     
    const handleGoogleLoginFailure = () => {
        console.log('Google Login Failed');
    };

    return (
        <GoogleOAuthProvider clientId="597389730622-1qsc90pogdcg8crjid8qsd0d20t2f1vi.apps.googleusercontent.com">
            <div className="login-page">
                <div className="login-container">
                    <div className="image-container">
                        <img src="/img/mainPaint.png" alt="Mother and child with onion character" className="login-image" />
                        <p className="image-caption">"   "<br />  .</p>
                    </div>
                    <div className="form-container">
                        <img src="/img/logo.png" alt="YANGPA Logo" className="logo"/>
                        <form onSubmit={handleLogin}>
                            <input
                                type="text"
                                placeholder=""
                                value={id}
                                onChange={(e) => setId(e.target.value)}
                            />
                            <input
                                type="password"
                                placeholder=""
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                            />
                            <div className="remember-me">
                                <input
                                    type="checkbox"
                                    id="rememberMe"
                                    checked={rememberMe}
                                    onChange={(e) => setRememberMe(e.target.checked)}
                                />
                                <label htmlFor="rememberMe"> </label>
                            </div>
                            <div className="findIDFW">
                                <GoogleLogin
                                    onSuccess={handleGoogleLoginSuccess}  //     
                                    onError={handleGoogleLoginFailure}    //     
                                    text="signin_with"
                                    shape="pill"
                                />
                            </div>
                            <div className="btnArea">
                                <button type="button" className="register-button"></button>
                                <button type="submit" className="login-button"></button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </GoogleOAuthProvider>
    );
};

export default LoginPage;
